import React, { useState, useEffect } from "react";
import { 
  Page, 
  Title, 
  Panel, 
  List, 
  Link, 
  Spacer, 
  Subtitle, 
  Loading, 
  Service 
} from "rsi-react-web-components";

import LguMasterTemplate from "../templates/LguMasterTemplate";
import { getModules } from "../modules";
import { getPartnerFromLocation } from "../lib/partner";


const styles = {
  list: {
    display: "flex",
    flexDirection: "column",
    alignItems: "flex-start",
  },
};

const PartnerServiceList = (props) => {
  const { modules, onSelect } = props;
  return (
    <React.Fragment>
      {modules.map((module) => {
        return (
          <Panel key={module.name}>
            <Subtitle>{module.title}</Subtitle>
            <List items={module.services} style={styles.list}>
              {({ item: service }) => (
                <Link key={service.name} component="button" onClick={() => onSelect(module, service)}>
                  {service.title}
                </Link>
              )}
            </List>
          </Panel>
        );
      })}
    </React.Fragment>
  );
};


const PartnerScreen = (props) => {
  const [partner, setPartner] = useState();
  const [modules, setModules] = useState([]);

  useEffect(() => {
    let partner = props.location.state ? props.location.state.partner : null;
    if (partner) {
      setPartner(partner);
      setModules(getModules(partner));
    } else {
      getPartnerFromLocation(props.location)
        .then(partner => {
          setPartner(partner);
          setModules(getModules(partner));
        }).catch(err => props.history.push("/partners"));
    }
  }, []);

  const onSelectService = (module, service) => {
    props.history.push({
      pathname: `/partner/${partner.name}/${module.name}/${service.name}`,
      state: { partner, module, service, },
    });
  };

  if (!partner) {
    return <Loading caption="Checking partner information" color="primary" />
  }

  return (
    <LguMasterTemplate partner={partner} location={props.location} history={props.history}>
      <Page>
        <Spacer height={20} />
        <Title>Select Transaction</Title>
        <Spacer height={20} />
        <PartnerServiceList modules={modules} onSelect={onSelectService} />
      </Page>
    </LguMasterTemplate>
  );
};

export default PartnerScreen;
